import Vue from 'vue'
import axios from 'axios'
import baseUrl from './baseUrl'
import qs from 'qs'
import router from '../router/index.js'

let token=localStorage.getItem('token')
let instance = axios.create({
  baseURL: baseUrl,
  headers: {
    'AccessToken':token,
    'Content-Type':"application/json;charset=UTF-8"
  }
});
// 添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前
   console.log(token)
  token=localStorage.getItem('token');
  config.headers.AccessToken = token;

  if (token != "") {

    return config;

  } else {

    // this.$router.push({path:'/loginIn'})

  }
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error);
});

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return response;
}, function (error) {
  // 对响应错误做点什么
  return Promise.reject(error);
});

function get(url, obj) {
    if(obj){
        url = url+"?"+qs.stringify(obj)
    }
    return new Promise((resolve, reject) => {
        instance
            .get(url)
            .then(res => {
                resolve(res.data)
            }, err => {
                reject(err)
            })
    })
}

function post(url, params) {
  return new Promise((resolve, reject) => {
    instance
      .post(url, params)
      .then(res => {
        resolve(res.data)
      }, err => {
        reject(err)
      })
  })
}

function post2(url, params,AccessToken) {
  return axios({
    method: 'post',
    baseURL:baseUrl,
    headers: {
      'AccessToken':AccessToken,
      'Content-Type':"application/json;charset=UTF-8"
    },
    url: url,
    data:params,
  })
}

function get2(url, params,AccessToken) {
  if(params){
    url = url+"?"+qs.stringify(params)
  }
  return axios({
    method: 'get',
    baseURL:baseUrl,
    headers: {
      'AccessToken':AccessToken,
      'Content-Type':"application/json;charset=UTF-8"
    },
    url: url,
  })
}

function get3(url, params,AccessToken) {
  if(params){
    url = url+"?"+qs.stringify(params)
  }
  return axios({
    method: 'get',
    baseURL:baseUrl,

    headers: {
      'Authorization':AccessToken,
      'Content-Type':"application/json;charset=UTF-8"
    },
    url: url,
  })
}

export default {
  get,
  post,
  post2,
  get2,
  get3
}



